  margin: 0;
  padding: 0; }

div, dl, dt, dd, ul, ol, li, h1, h2, h3, h4, h5, h6, form, fieldset, p, button, iframe, th, td {
  margin: 0;
  padding: 0; }

article, aside, details, figcaption, figure, footer, header, hgroup, nav, section, summary {
  display: block;
  margin: 0;
  padding: 0; }

h1, h2, h3, h4, h5, h6, dt {
  font-weight: normal; }

img, fieldset, iframe {
  border: 0 none; }

button {
  border: 0 none;
  cursor: pointer;
  background: none; }
  button:focus {
    outline: 0; }

li {
  list-style: none; }

button {
  border: 0 none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

th, td {
  border-collapse: collapse; }

legend, caption, hr {
  display: none; }

i, em, u, cite {
  font-style: normal; }

strong {
  font-weight: normal; }

img {
  vertical-align: top;
  max-width: 100%; }

a {
  color: #4b4c4c;
  text-decoration: none;
  outline-offset: -1px; }

video {
  position: relative;
  width: 100%;
  height: 100%; }

/* common */
